import "./App.css";
import React, { useState } from "react";
import Modal from "react-modal";

import { caKru } from "./data/datacakru.json";
import { kru } from "./data/datakru.json";

import Cardlistcakru from "./components/Cardlistcakru";
import Cardlistkru from "./components/Cardlistkru";
import ModalBody from "./components/Modal";

function App() {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedCaKru, setSelectedCakru] = useState({});

  const openModal = (CaKru) => {
    setModalIsOpen(true);
    setSelectedCakru(CaKru);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setSelectedCakru({});
  };
  return (
    <div className="App">
      <header className="header">
        Tugas Wawancara ARC Kevin Ryan 13519191
      </header>
      <div className="PreviewCards">
        {caKru.map((nameCaKru, index) => {
          return (
            <div
              key={`${nameCaKru.nama}-${index}`}
              className="NamaCaKru"
              onClick={() => openModal(nameCaKru)}
            >
              <Cardlistcakru {...nameCaKru} />
            </div>
          );
        })}

        {kru.map((namaKru, index) => {
          return (
            <div
              key={`${namaKru.nama}-${index}`}
              className="NameKru"
              onClick={() => openModal(namaKru)}
            >
              <Cardlistkru {...namaKru}></Cardlistkru>
            </div>
          );
        })}

        <Modal isOpen={modalIsOpen}>
          <button onClick={closeModal}> Exit </button>
          {selectedCaKru && <ModalBody {...selectedCaKru} />}
        </Modal>
      </div>
    </div>
  );
}

export default App;
